import { Component, OnInit } from '@angular/core';
import { FormControl , FormGroup } from '@angular/forms'
import { AgregarProductosService } from '../services/agregar-productos.service';
import {CalcularService} from '../services/calcular.service'
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import {Producto} from './../interfaces/producto'

@Component({
  selector: 'app-editar-producto',
  templateUrl: './editar-producto.component.html',
  styleUrls: ['./editar-producto.component.css']
})
export class EditarProductoComponent implements OnInit {
  
  parametro
  sucess =false
  form: FormGroup
   private buildForm() {
    this.form = new FormGroup({
      _id : new FormControl(''),
    	nombre : new FormControl(''),
  		precioCosto : new FormControl(''),
 		  porcentajeGanancia : new FormControl(''),
  		precioFinal : new FormControl(''),
    })	
  
}
  constructor( private http:AgregarProductosService,
  	           private route: ActivatedRoute,
  	           private router: Router
  	         , private calcularService: CalcularService) 
             { this.buildForm()}

  ngOnInit(): void {
  	this.form.get("porcentajeGanancia").valueChanges.subscribe(value=>{
      this.calcularService.obtenerPrecioFinal(
        this.form.get("precioCosto").value,
        this.form.get("porcentajeGanancia").value,
        this.form.get("precioFinal"))
  	
  })
  	this.form.get("precioCosto").valueChanges.subscribe(value=>{
  	this.calcularService.obtenerPrecioFinal(
      this.form.get("precioCosto").value,
        this.form.get("porcentajeGanancia").value,
        this.form.get("precioFinal"))
  })
  	this.route.params.subscribe(params => {
    this.parametro = params['id'];
    console.log(this.parametro)
    this.http.getOneProduct<Producto>(this.parametro)
       .subscribe((value:Producto )=> {console.log(value)
          this.form.get("_id").setValue(value._id)
       		this.form.get("nombre").setValue(value.nombre)
  		    this.form.get("precioCosto").setValue(value.precioCosto)
  		    this.form.get("porcentajeGanancia").setValue(value.porcentajeGanancia)
  		    this.form.get("precioFinal").setValue(value.precioFinal)
       	})
    });
  }
  
  actualizar(event: Event) {
  event.preventDefault();
  const value = this.form.value;
  this.http.actualizarProducto(value,this.form.get("_id").value)
  .subscribe(producto =>{ 
  		this.sucess=true
    	 setTimeout(()=>{ this.sucess=false},3000)
  })
}
	
}
