import { Component, OnInit } from '@angular/core';
import { AgregarProductosService } from '../services/agregar-productos.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  title = 'searcher';
  public search: string = '';
  public lista;
  constructor(private http:AgregarProductosService) { }

  ngOnInit(): void {
    this.http.getAllProducts()
     .subscribe(value =>{ this.lista = value})
  }
  
  onSearchPokemon( search: string ) {
    this.search = search;
  }
   eliminar(id){
     this.http.deleteProducto(id)
     .subscribe(value => {
       let productIndex = this.lista.findIndex(item => item._id === id)
       this.lista.splice(productIndex,1)
       console.log('eliminado')})
   }
}
