import { Component, OnInit } from '@angular/core';
import { FormControl , FormGroup } from '@angular/forms'
import { AgregarProductosService } from '../services/agregar-productos.service';
import {CalcularService} from '../services/calcular.service'

@Component({
  selector: 'app-agregar-producto',
  templateUrl: './agregar-producto.component.html',
  styleUrls: ['./agregar-producto.component.css']
})
export class AgregarProductoComponent implements OnInit {
  
  sucess =false
  form: FormGroup
   private buildForm() {
    this.form = new FormGroup({
    	nombre : new FormControl(''),
  		precioCosto : new FormControl(''),
 		porcentajeGanancia : new FormControl(''),
  		precioFinal : new FormControl(''),
    })	
  
}
  constructor(private http:AgregarProductosService, private calcularService: CalcularService) { 
  		this.buildForm();
  }

  ngOnInit(): void {
  	this.form.get("porcentajeGanancia").valueChanges.subscribe(value=>{
      this.calcularService.obtenerPrecioFinal(
        this.form.get("precioCosto").value,
        this.form.get("porcentajeGanancia").value,
        this.form.get("precioFinal"))
  	
  })
  	this.form.get("precioCosto").valueChanges.subscribe(value=>{
  	this.calcularService.obtenerPrecioFinal(
      this.form.get("precioCosto").value,
        this.form.get("porcentajeGanancia").value,
        this.form.get("precioFinal"))
  })
  }
  save(event: Event) {
  event.preventDefault();
  const value = this.form.value;
  this.http.agregarProducto(value)
  .subscribe(producto =>{ 
  		this.form.get("nombre").setValue('')
  		this.form.get("precioCosto").setValue('')
  		this.form.get("porcentajeGanancia").setValue('')
  		this.form.get("precioFinal").setValue('')
  		this.sucess=true
    	 setTimeout(()=>{ this.sucess=false},2000)
  })
}
	
}
